@font-face {
    font-family: Nanum Gothic;
    src: url("./fonts/nanumgothicecor.ttf");
}

select {
    transition-duration: .18s;
    width: 70px;
    height: 37px;
    margin-left: 6%;
    flex-shrink: 0;
    font-family: "Nanum Gothic", fantasy;
    outline: 0;
    border: 1.3px solid #dfe1e5;
    appearance: auto;
    -moz-appearance: none;
    -webkit-appearance: none;
    border-radius: 50px;
    text-align-last: center;
    text-align: center;
    -ms-text-align-last: center;
    -moz-text-align-last: center;
    cursor: pointer;
    background-color: white;
}

select::-ms-expand {
    display: none;
}

input {
    transition-duration: .18s;
    height: 35px;
    outline: 0;
    border: 1.3px solid #dfe1e5;
    border-radius: 50px;
    text-align: center;
    font-family: "Nanum Gothic", fantasy;
    font-size: 12px;
    margin-left: 2%;
    cursor: text;
}

input:hover, select:hover {
    box-shadow: 0 0 3.5px gray;
}

.subbox {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
}

.btn {
    width: 80px;
    height: 35px;
    margin-left: 2%;
    margin-right: 8%;
    font-family: "Nanum Gothic", fantasy;
    font-size: 14px;
    border: 1.3px solid #dfe1e5;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition-duration: 0.2s;
    flex-shrink: 0;
    cursor: pointer;
}

.btn:hover {
    opacity: 0.7;
    box-shadow: 0 0 3.5px gray;
}

.mainbox {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 100px;
}

.box {
    margin-top: 30px;
    width: 20vw;
    height: 20vw;
    min-width: 250px;
    min-height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: silver 1px solid;
}

.box * {
    position: absolute;
}

.img {
    width: 20vw;
    height: 20vw;
    min-width: 250px;
    min-height: 250px;
    background-color: white;
}